//配置路由
import Vue from "vue";
import VueRouter from "vue-router";
import routes from './routes'
//使用插件
Vue.use(VueRouter);
//引入store，获取token
import store from '@/store'
//配置路由
//原因：push是一个promise，promise需要传递成功和失败两个参数，我们的push中没有传递。
// 方法：this.$router.push({name:‘Search’,params:{keyword:“…”||undefined}},()=>{},()=>{})后面两项分别代表执行成功和失败的回调函数。
//这种写法治标不治本，将来在别的组件中push|replace,编程式导航还是会有类似错误
//1、先把VueRouter原型对象的push，保存一份
let originPush = VueRouter.prototype.push;
//2、重写push|replace
//第一个参数：告诉原来的push，跳转的目标位置和传递了哪些参数
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        originPush.call(this, location, resolve, reject)
    } else {
        originPush.call(this, location, () => { }, () => { })
    }
}
VueRouter.prototype.replace = function (location, resolve, reject) {
    if (resolve && reject) {
        originReplace.call(this, location, resolve, reject);
    } else {
        originReplace.call(
            this,
            location,
            () => { },
            () => { }
        );
    }
};

//

let router = new VueRouter({
    mode: 'history', // history模式，移除url中#号   
    routes,
    scrollBehavior(to, from, savedPosition) {
        // 始终滚动到顶部
        return { y: 0 }
    },
});
//全局守卫：前置守卫（跳转之前判断
router.beforeEach(async (to, from, next) => {
    // https://router.vuejs.org/zh/guide/advanced/navigation-guards.html#%E5%85%A8%E5%B1%80%E5%89%8D%E7%BD%AE%E5%AE%88%E5%8D%AB
    // to:获取跳转至路由的信息
    // from:获取从哪里来的路由信息
    // next:放行函数 | next(path) 放行到指定位置 | next(false)
    // next();
    // console.log(store.state.user.token)
    let token = store.state.user.token;
    let username = store.state.user.userInfo.name;
    //用户已登录
    if (token) {
        // login重定向至首页
        if (to.path == '/login' || to.path == '/register') {
            next('/home')
        } else {
            if (username) {
                next()
            } else {
                //获取用户信息
                try {
                    //成功
                    await store.dispatch("user/getUserInfo")
                    next()
                } catch (error) {
                    // 没获取到用户名、token过期，清除信息，提示去登录                    
                    await store.dispatch('user/userLogout')
                    // alert('登录信息已失效，请重新登录')
                    next('/login')
                }
            }
        }
    } else {
        //用户未登录：仅可去首页，注册，登录，搜索，购物车，商品
        let toPath = to.path;
        if (toPath.indexOf('/trade') != -1 || toPath.indexOf('/pay') != -1 || toPath.indexOf('/center') != -1) {
            //把未登录的时候想去而没有去成的信息，存包处于地址栏中【路由】
            next('/login?redirect=' + toPath)
        } else {
            next()
        }
        // 
    }
})


export default router;