//引入路由
// import Home from '@/pages/Home'
// import Search from '@/pages/Search'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
import Center from '@/pages/Center'
//引入二级路由组件
import MyOrder from '@/pages/Center/myOrder'
import GroupOrder from '@/pages/Center/groupOrder'


//
export default [

    {
        path: "/center",
        name: 'Center',
        component: Center,
        meta: { show: true },
        //二级路由
        children: [
            {
                path: "myorder",
                name: 'MyOrder',
                component: MyOrder
            },
            {
                path: "grouporder",
                name: 'GroupOrder',
                component: GroupOrder
            },
            {
                path: "/center",
                redirect: '/center/myorder'
            }
        ]
    },
    {
        path: "/paySuccess",
        name: 'PaySuccess',
        component: PaySuccess,
        meta: { show: true }
    },
    {
        path: "/pay",
        name: 'Pay',
        component: Pay,
        meta: { show: true },
        beforeEnter: (to, from, next) => {
            // ...
            if (from.path == '/trade') {
                next()
            } else {
                next(false)
            }
        }
    },
    {
        path: "/trade",
        name: 'Trade',
        component: Trade,
        meta: { show: true },
        //路由独享守卫 软件
        beforeEnter: (to, from, next) => {
            // ...
            if (from.path == '/shopcart') {
                next()
            } else {
                next(false)
            }
        }
    },

    {
        path: "/shopcart",
        name: 'ShopCart',
        component: ShopCart,
        meta: { show: true }
    },
    {
        path: "/addcartsuccess",
        name: 'AddCartSuccess',
        component: AddCartSuccess,
        meta: { show: true }
    },
    {
        path: "/detail/:skuid",
        component: Detail,
        meta: { show: true }
    },
    {
        path: "/home",
        // () => import("@/pages/Home") 路由懒加载
        component: () => import("@/pages/Home"),
        meta: { show: true }
    },
    {
        path: "/search/:keyword?",
        component: () => import("@/pages/Search"),
        meta: { show: true },
        name: "search"
    },
    {
        path: "/login",
        component: Login,
        meta: { show: false }
    },
    {
        path: "/register",
        component: Register,
        meta: { show: false }
    },

    //重定向，访问/ ，定向到首页
    {
        path: "*",
        redirect: '/home'
    }
]