import { reqCategoryList, reqGetBannerList, reqGetFloorList } from '@/api'
//home仓库
const state = {
    categoryList: [],
    bannerList: [],
    floorList: []
}
const mutations = {
    CETGORYLIST(state, categoryList) {
        state.categoryList = categoryList
    },
    CETBANNERLIST(state, bannerList) {
        state.bannerList = bannerList
    },
    CETFLOORLIST(state, floorList) {
        state.floorList = floorList
    }
}
const actions = {
    //通过API里的接口调用，向服务器发请求，获取数据
    async categoryList({ commit }) {
        let result = await reqCategoryList()
        // console.log(result)
        if (result.code == 200) {
            commit('CETGORYLIST', result.data)
        }
    },

    async getBannerList({ commit }) {
        let result = await reqGetBannerList()
        // console.log(result)
        if (result.code == 200) {
            commit('CETBANNERLIST', result.data)
        }
    },
    async getFloorList({ commit }) {
        let result = await reqGetFloorList()
        if (result.code == 200) {
            commit('CETFLOORLIST', result.data)
        }
    }
}
const getters = {}

export default {
    namespaced: true, state, mutations, actions, getters
}