import { reqCartList, reqDeleteCartById, reqUpdateCheckedById } from '@/api'
const state = {
    cartList: []
}
const mutations = {
    GETCARTLIST(state, cartList) {
        state.cartList = cartList;
    }
}
const actions = {
    //获取购物车数据
    async getCartList({ commit }) {
        let reslut = await reqCartList();
        // console.log(reslut)
        if (reslut.code == 200) {
            commit('GETCARTLIST', reslut.data)
        }
    },
    //删除购物车数据
    async deleteCartListBySkuId({ commit }, skuId) {
        let reslut = await reqDeleteCartById(skuId);
        if (reslut.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error('faile'));
        }
    },
    //修改购物车某一个产品的选中状态
    async updateCheckedById({ commit }, { skuId, isChecked }) {
        let reslut = await reqUpdateCheckedById(skuId, isChecked);
        if (reslut.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error('faile'));
        }
    },
    //删除全部勾选的产品,调用deleteCartListByS kuId函数
    deleteAllCheckedCart({ dispatch, getters }) {
        // console.log(context)
        // context小仓库:commit【提交mutations修改state】 getters【计算属性】 dispatch【派发action】 state【当前仓库数据】
        let PromiseAll = [];
        getters.cartList.cartInfoList.forEach(item => {
            let promise = item.isChecked == 1 ? dispatch('deleteCartListBySkuId', item.skuId) : '';
            //将返回的promise添加到数组
            PromiseAll.push(promise);
        });
        return Promise.all(PromiseAll);
    },
    //修改全部产品的选中状态
    updateAllCartIsChecked({ dispatch, state }, isChecked) {
        // console.log(state);
        // console.log(isChecked);
        let PromiseAll = [];
        state.cartList[0].cartInfoList.forEach(item => {
            let promise = dispatch('updateCheckedById', { skuId: item.skuId, isChecked });
            PromiseAll.push(promise);
            // item.isChecked = 0 ? dispatch('1') : "1"
        })
        return Promise.all(PromiseAll);
    }
    //勾选购物车数据
}
const getters = {
    cartList(state) {
        return state.cartList[0] || {}
    },
}
export default {
    namespaced: true, state, mutations, actions, getters
}
