import { reqAddressInfo, reqOrderInfo } from "@/api"
const state = {
    address: [],
    orderInfo: {}
}
const mutations = {
    GETUSERADDRESSINFO(state, address) {
        state.address = address
    },
    GETUSERORDERINFO(state, orderInfo) {
        state.orderInfo = orderInfo
    }
}
const actions = {
    //获取用户收货地址信息
    async getUserAddressInfo({ commit }) {
        let result = await reqAddressInfo();
        if (result.code == 200) {
            commit('GETUSERADDRESSINFO', result.data)
        } else {
            return Promise.reject(new Error('faile'))
        }
    },
    //获取提交商品信息
    async getUserOrderInfo({ commit }) {
        let result = await reqOrderInfo();
        if (result.code == 200) {
            commit('GETUSERORDERINFO', result.data)
        } else {
            return Promise.reject(new Error('faile'))
        }
    }
}
const getters = {}
export default {
    namespaced: true, state, mutations, actions, getters
}